<template>
  <section class="background">
    <HeaderBar></HeaderBar>
    <router-view :key="$route.path" />
    <FooterBar></FooterBar>
    <!-- <nav>
    <router-link to="/">Home</router-link> |
    <router-link to="/about">About</router-link>
  </nav> -->
  </section>
</template>

<script>
import HeaderBar from "@/components/HeaderBar.vue";
import FooterBar from "./components/FooterBar.vue";
import axios from "axios";
import qs from "qs";
import crypto from "crypto-js";
import { reactive } from "vue";
let THIS;
let userInfor = {};

export default {
  name: "HomeView",
  components: {
    // HelloWorld,
    HeaderBar,
    FooterBar,
  },
  mounted() {
    THIS = this;
  },
  provide() {
    return {
      refresh_token_func: this.refreshToken,
      setCookie_token_func: this.setCookieToken,
      login_PC_func: this.loginPC,
      user_information_obj: this.userInformation,
    };
  },
  methods: {
    refreshToken(tokenData, fCallback) {
      let params = {
        grant_type: "refresh_token",
        client_id: "97e67934-92a6-4f26-a2f6-5cecc2f380d5",
        refresh_token: this.$cookies.get("MURA_refresh_token"),
        scope: "game-site",
        prefix: "game-site",
      };
      axios
        .post("https://para-member-dev.para-daily.com/oauth/token", params)
        .then((resp) => {
          const { expires_in, access_token, refresh_token } = resp.data;
          tokenData.expires_in = expires_in;
          tokenData.access_token = access_token;
          tokenData.refresh_token = refresh_token;
          THIS.setCookieToken(tokenData);
          fCallback(access_token);
          // THIS.getUserData(access_token);
        })
        .catch((e) => {
          console.log(e);
        });
    },
    setCookieToken: function (tokenData) {
      THIS.$cookies.set(
        "MURA_access_token",
        tokenData.access_token,
        tokenData.expires_in - 60 * 60 * 24
      );
      THIS.$cookies.set(
        "MURA_refresh_token",
        tokenData.refresh_token,
        tokenData.expires_in * 2 - 60 * 60 * 24
      );
    },
    loginPC() {
      const createRandomString = (num) => {
        return [...Array(num)]
          .map(() => Math.random().toString(36)[2])
          .join("");
      };
      const base64Url = (string) => {
        return string
          .toString(crypto.enc.Base64)
          .replace(/\+/g, "-")
          .replace(/\//g, "_")
          .replace(/=/g, "");
      };
      const urlData = reactive({
        response_type: "code",
        client_id: "97e67934-92a6-4f26-a2f6-5cecc2f380d5",
        redirect_uri: window.location.origin + "/#/login",
        scope: "game-site",
        prefix: "game-site",
        state: "",
        code_challenge: "",
        code_challenge_method: "S256",
      });
      urlData.state = createRandomString(40);
      const verifier = createRandomString(128);
      urlData.code_challenge = base64Url(crypto.SHA256(verifier));
      window.localStorage.setItem("state", urlData.state);
      window.localStorage.setItem("verifier", verifier);
      console.log(urlData);
      const url =
        "https://para-member-dev.para-daily.com/oauth/authorize?" +
        qs.stringify(urlData);
      open(url, "LoginWindow", "width=700,height=700");
    },
    userInformation: function (datas) {
      if (datas) {
        userInfor = datas;
      } else {
        return userInfor;
      }
    },
  },
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.background {
  position: relative;
  padding: 0;
}
</style>
