<template>
  <CoverBuyView :item="item" :closeFun="closeDialog" v-if="isBuyCover">
  </CoverBuyView>
  <main :style="this.isLoading">
    <div class="container">
      <ShadowBox>
        <div class="container game_detailBox">
          <div class="row">
            <div class="col titleBox">
              <TextTitle :button="false">{{ item.title }}</TextTitle>
            </div>
          </div>
          <div class="row">
            <div class="col col-lg-7 col-12 imgs_show_ctrl">
              <div class="show">
                <img :src="item.snapshots[0]" />
              </div>
              <div class="choose_bar">
                <SelectBtn type="left" :func="leftBtnFunc"></SelectBtn>
                <div class="imgBoxs">
                  <img
                    v-for="(url, index) in item.snapshots"
                    :src="url"
                    :key="index"
                    @click="changeImgshow($event, item.snapshots, index)"
                  />
                </div>
                <SelectBtn type="right" :func="rightBtnFunc"></SelectBtn>
              </div>
            </div>
            <div class="col col-lg-5 col-12">
              <div class="container-fluid h-100 rightSide">
                <div class="row excerptRow">
                  <div
                    class="col_thumbnail col col-xxl-3 col-xl-4 col-lg-5 col-md-3 col-sm-4 col-12"
                  >
                    <img :src="item.thumbnail" />
                  </div>
                  <div
                    class="col col-xxl-9 col-xl-8 col-lg-7 col-md-9 col-sm-8 col-12"
                  >
                    <p>{{ item.description }}</p>
                  </div>
                </div>
                <div class="row platform_btn">
                  <div class="col col-12" v-if="!gameBought">
                    <p class="game_price">
                      {{ item.platforms.pc.price + " MPoint" }}
                    </p>
                  </div>
                  <div class="col col-12">
                    <div class="platform_title">
                      <h5>{{ $t("system.platform") }}</h5>
                    </div>
                  </div>
                  <div
                    class="col col-lg-3 col-md-3 col-4"
                    v-if="item.platforms.ios"
                  >
                    <a :href="item.platforms.ios.url" target="_blank">
                      <img src="@/assets/IosBtn.svg"
                    /></a>
                  </div>
                  <div
                    class="col col-lg-3 col-md-3 col-4"
                    v-if="item.platforms.android"
                  >
                    <a :href="item.platforms.android.url" target="_blank">
                      <img src="@/assets/AndroidBtn.svg"
                    /></a>
                  </div>
                  <div
                    class="col col-lg-3 col-md-3 col-4"
                    v-if="item.platforms.web"
                  >
                    <a :href="item.platforms.web.url" target="_blank">
                      <img src="@/assets/PlayBtn.svg" />
                    </a>
                  </div>
                  <div
                    class="col col-lg-3 col-md-3 col-4"
                    v-if="item.platforms.pc"
                    @click="pcBuyBtnClick"
                  >
                    <PcBuyBtn :gameBought="gameBought"></PcBuyBtn>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="row">
            <div class="col col-12">
              <div class="des_box">
                <div class="title">
                  <img class="icon" alt="" src="@/assets/HalfCircle-gray.svg" />
                  <h2>{{ $t("system.gameDescription") }}</h2>
                </div>
                <hr />
                <p>{{ item.content }}</p>
              </div>
            </div>
          </div>
        </div>
      </ShadowBox>
    </div>
    <section class="container moreGamesDiv">
      <TextTitle href="/Games1">{{ $t("system.moreGames") }}</TextTitle>
      <div class="row">
        <GameList
          :type="0"
          :direction="0"
          :datas="games"
          :icon="true"
        ></GameList>
      </div>
    </section>
  </main>
</template>

<script>
import { useI18n } from "vue-i18n";
import axios from "axios";
import $ from "jquery";
import SelectBtn from "@/components/control/SelectBtn.vue";
import GameList from "@/components/control/GameList.vue";
import TextTitle from "@/components/control/TextTitle.vue";
import ShadowBox from "@/components/control/ShadowBox.vue";
import CoverBuyView from "@/components/control/CoverBuyView.vue";
import PcBuyBtn from "@/components/svg/PcBuyBtn.vue";
let THIS;
let oAxiosHeaders;

export default {
  inject: ["refresh_token_func", "setCookie_token_func", "login_PC_func"],
  components: {
    GameList,
    SelectBtn,
    TextTitle,
    ShadowBox,
    CoverBuyView,
    PcBuyBtn,
  },
  data() {
    return {
      isBuyCover: false,
      isLoading: "visibility:hidden;",
      item: {
        id: 1,
        title: "遊戲1",
        description: "遊戲說明1",
        thumbnail:
          "https://mura-gaming-dev.s3.ap-northeast-1.amazonaws.com/1_2559960a03.gif",
        snapshots: [
          "https://mura-gaming-dev.s3.ap-northeast-1.amazonaws.com/11_95c91d93c3.jpg",
          "https://mura-gaming-dev.s3.ap-northeast-1.amazonaws.com/1_0f504473ea.jpg",
        ],
        platforms: {
          pc: "https://www.google.com/",
          web: "https://news.para-daily.com/",
          android: "https://play.google.com/",
          ios: "https://www.apple.com/tw/app-store/",
        },
        links: {
          discord:
            "https://discord.com/channels/787207692896567306/787212519596490773",
        },
        content: "遊戲內容1",
        events: [],
      },
      games: [],
      gameBought: false,
    };
  },
  mounted() {
    const { locale } = useI18n();
    THIS = this;
    oAxiosHeaders = {
      language: locale.value,
    };
    axios
      .post(
        "https://gaming-dev.murax2.com/api/getdata",
        {
          header: oAxiosHeaders,
          requests: [
            {
              action: "getGameData",
              data: {
                id: this.$route.query.id,
              },
            },
            {
              action: "listGames",
              data: {
                length: 6,
              },
            },
          ],
        },
        { headers: { "Content-Type": "application/json" } }
      )
      .then(function (res) {
        let actionDatas = res.data.responses;
        THIS.item = actionDatas[0].data;
        THIS.games = actionDatas[1].data;
        THIS.checkGameBought();
      })
      .catch(function (error) {
        console.log(error);
      });
  },
  beforeUpdate() {
    THIS.checkGameBought();
  },
  methods: {
    leftBtnFunc: function (event) {
      let $imgBoxs = $(event.target).parents(".choose_bar").find(".imgBoxs");
      let aImgs = $imgBoxs.find("img");
      var style = aImgs[0].currentStyle || window.getComputedStyle(aImgs[0]);
      $imgBoxs.animate(
        {
          scrollLeft:
            $imgBoxs.scrollLeft() -
            (aImgs[0].width +
              parseInt(style.marginLeft) +
              parseInt(style.marginRight)),
        },
        500
      );
      // console.log(aImgs);
    },
    rightBtnFunc: function (event) {
      let $imgBoxs = $(event.target).parents(".choose_bar").find(".imgBoxs");
      let aImgs = $imgBoxs.find("img");
      var style = aImgs[0].currentStyle || window.getComputedStyle(aImgs[0]);
      $imgBoxs.animate(
        {
          scrollLeft:
            $imgBoxs.scrollLeft() +
            (aImgs[0].width +
              parseInt(style.marginLeft) +
              parseInt(style.marginRight)),
        },
        500
      );
      // console.log($imgBoxs);
    },
    changeImgshow: function (event, aItems, index) {
      let $img = $(".game_detailBox").find(".show").find("img");
      $img.attr("src", aItems[index]);
      $(event.target);
      // console.log(aItems[index]);
    },
    pcBuyBtnClick: function () {
      if (this.$cookies.get("MURA_access_token")) {
        if (THIS.gameBought) {
          oAxiosHeaders["accessToken"] = THIS.$cookies.get("MURA_access_token");
          axios
            .post(
              "https://gaming-dev.murax2.com/api/getdata",
              {
                header: oAxiosHeaders,
                requests: [
                  {
                    action: "getGameUrl",
                    data: {
                      id: THIS.item.id,
                      platform: "pc",
                    },
                  },
                ],
              },
              { headers: { "Content-Type": "application/json" } }
            )
            .then(function (res) {
              if (res.data.responses[0])
                window.open(res.data.responses[0].data.url);
            })
            .catch(function (error) {
              console.log(error);
            });
        } else {
          window.scrollTo(0, 0);
          this.isBuyCover = true;
        }
      } else {
        this.login_PC_func();
      }
    },
    closeDialog: function () {
      this.isBuyCover = false;
    },
    checkGameBought: function () {
      if (THIS.$cookies.get("MURA_access_token")) {
        oAxiosHeaders["accessToken"] = THIS.$cookies.get("MURA_access_token");
        axios
          .post(
            "https://gaming-dev.murax2.com/api/getdata",
            {
              header: oAxiosHeaders,
              requests: [
                {
                  action: "checkBoughtGames",
                  data: {
                    ids: [THIS.item.id],
                  },
                },
              ],
            },
            { headers: { "Content-Type": "application/json" } }
          )
          .then(function (res) {
            let code = res.data.result.code;
            if (code == "00") {
              let actionDatas = res.data.responses;
              THIS.gameBought = actionDatas[0].data.bought_games.length != 0;
              THIS.isLoading = "visibility:visible;";
            } else if (code == "13") {
              THIS.refresh_token_func();
              THIS.checkGameBought();
            }
          })
          .catch(function (error) {
            console.log(error);
          });
      } else {
        THIS.isLoading = "visibility:visible;";
      }
    },
  },
};
</script>

<style scoped lang="scss">
main {
  background-color: white;
  padding: 1em;
}
.game_detailBox {
  margin-top: 1em;
  .titleBox {
    margin: 0.4em 0;
  }
}
.imgs_show_ctrl {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  .show img {
    width: 100%;
    border-radius: 15px;
  }
  .choose_bar {
    height: 100%;
    display: flex;
    align-items: center;
    margin-top: 10px;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
    .imgBoxs {
      display: flex;
      justify-content: flex-start;
      flex-direction: row;
      width: calc(100% - 70px);
      overflow: hidden;
      img {
        cursor: pointer;
        margin-left: 5px;
        margin-right: 5px;
        -webkit-transition: all 0.5s;
        -moz-transition: all 0.5s;
        -o-transition: all 0.5s;
        transition: all 0.5s;
        &:hover {
          opacity: 0.8;
          box-shadow: 0px 0px 10px 1px #fa79fe;
        }
      }
    }
    img {
      height: 60px;
      border-radius: 10px;
    }
    > * {
      margin: 0 0.5em;
    }
  }
}
.excerptRow {
  .col {
    display: flex;
  }
  img {
    width: 100%;
    max-width: 150px;
    max-height: 150px;
    border-radius: 15px;
  }
  p {
    text-align: left;
    padding: 0 15px;
    color: #9fa0a0;
    font-weight: bold;
    font-size: 1.2em;
  }
}
.platform_title {
  text-align: left;
  margin-top: 5px;
  padding: 10px 0;
  > h5 {
    color: #595757 !important;
  }
}
.des_box {
  padding: 0 20px;
  border: 2px solid #ff3885;
  border-radius: 20px;
  margin: 30px 0;
  .title {
    display: flex;
    align-items: center;
    margin-top: 2em;
    margin-bottom: 1em;
    justify-content: flex-start;
    .icon {
      height: 5vh;
    }
    h2 {
      margin: 0 1vw;
      color: #595757 !important;
    }
  }
  > p {
    white-space: pre-line;
    text-align: left;
    margin: 0 10px;
    color: #595757;
    font-weight: bold;
    padding-bottom: 50px;
  }
}
.moreGamesDiv {
  margin-top: 40px;
}
.rightSide {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  .col {
    padding: 0 2px;
  }
}
.platform_btn {
  .game_price {
    text-align: left;
    margin-bottom: unset;
    font-weight: bold;
    color: #ff3885;
    font-size: 1.8em;
  }
  img {
    cursor: pointer;
    max-height: 90px;
    border-radius: 16px;
    margin: 5px 0;
    box-shadow: 1px 3px 5px 0px #d3d1d0;
    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
    &:hover {
      opacity: 0.8;
      transform: scale(0.95);
    }
    &:active {
      opacity: 1;
      transform: scale(1.05);
    }
  }
}

@media (max-width: 768px) {
  .choose_bar {
    overflow-y: auto;
    > * {
      margin: 0;
    }
  }
}
@media (max-width: 992px) {
  .excerptRow {
    margin-top: 2em;
    margin-bottom: 1em;
  }
  .rightSide {
    padding: 0;
    .row {
      margin-left: 0;
      margin-right: 0;
    }
  }
}
@media (max-width: 576px) {
  main {
    padding-left: 0.4em;
    padding-right: 0.4em;
  }
  .choose_bar {
    .imgBoxs {
      overflow: auto !important;
    }
  }
  .excerptRow {
    .col_thumbnail {
      justify-content: center;
    }
    p {
      font-size: 1em;
      margin-top: 1em;
    }
  }
  .des_box {
    .icon {
      height: 3vh !important;
    }
  }
}
</style>
