<template>
  <section @click="closeFun">
    <div class="buy_main_div" @click.stop="">
      <div class="close_btn_bar">
        <button class="bi bi-x-lg btn btn-pink" @click="closeFun"></button>
      </div>
      <div class="container">
        <div class="row">
          <div class="col-4">
            <img :src="item.thumbnail" />
          </div>
          <div class="col-8">
            <p>{{ item.description }}</p>
            <p class="price">
              {{ item.platforms[platform].price + " MPoint" }}
            </p>
          </div>
        </div>
        <hr />
        <div class="row point_row">
          <div class="col-4">
            <p>{{ $t("system.accountPoints") + "：" }}</p>
          </div>
          <div class="col-8 price_col">
            <p>{{ this.user_information_obj().total_points + " MPoint" }}</p>
          </div>
        </div>
        <div class="row point_row">
          <div class="col-4">
            <p>{{ $t("system.sellPoints") + "：" }}</p>
          </div>
          <div class="col-8 price_col">
            <p class="sell">
              {{ "- " + item.platforms[platform].price + " MPoint" }}
            </p>
          </div>
        </div>
        <hr />
        <div class="row point_row">
          <div class="col-4">
            <p>{{ $t("system.balancePoints") + "：" }}</p>
          </div>
          <div class="col-8 price_col">
            <p>
              {{ getBalancePoints() + " MPoint" }}
            </p>
          </div>
        </div>
        <div class="row btn_row">
          <div class="col">
            <button type="button" class="btn btn-pink" @click="buyNowFun">
              {{ $t("system.buyNow") }}
            </button>
          </div>
        </div>
        <div class="row">
          <div class="col notice_content">
            <p class="">
              {{ $t("system.buyNotice1") }}
            </p>
            <p class="">
              {{ $t("system.buyNotice2") }}
            </p>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import axios from "axios";
let THIS;
export default {
  inject: ["user_information_obj"],
  name: "CoverBuyView",
  props: {
    item: {
      default: function () {
        var obj = {};
        return obj;
      },
      type: Object,
    },
    platform: { default: "pc", type: String },
    closeFun: Function,
  },
  data() {
    return {};
  },
  mounted() {
    THIS = this;
  },
  methods: {
    getBalancePoints: function () {
      return (
        this.user_information_obj().total_points -
        this.item.platforms[this.platform].price
      );
    },
    buyNowFun: function () {
      axios
        .post(
          "https://gaming-dev.murax2.com/api/setdata",
          {
            header: {
              accessToken: THIS.$cookies.get("MURA_access_token"),
            },
            request: {
              action: "buyGame",
              data: {
                id: THIS.item.id,
                platform: THIS.platform,
              },
            },
          },
          { headers: { "Content-Type": "application/json" } }
        )
        .then(function (res) {
          if (res.data.response) {
            let data = res.data.response.data;
            if (data.code == "00") {
              let newDatas = THIS.user_information_obj();
              newDatas.remaining_point = data.remaining_point;
              newDatas.total_points =
                Number(data.remaining_point.paid_points) +
                Number(data.remaining_point.free_points);
              THIS.user_information_obj(newDatas);
              alert(THIS.$t("system.buyOrderSuccess"));
              THIS.closeFun();
            } else if (data.code == "01") {
              alert(THIS.$t("system.pointsNotEnough"));
              window.open("https://para-member-dev.para-daily.com/purchase");
              THIS.closeFun();
            } else {
              alert(THIS.$t("system.orderFailPleaseContactUs"));
            }
          } else if (res.data.result) {
            let result = res.data.result;
            if (result.code == "20") alert(THIS.$t("system.buyOrderFail"));
            // console.log(result.code + "////" + result.message);
          }
        })
        .catch(function (error) {
          console.log(error);
        });
    },
  },
};
</script>

