<template>
  <header class="headerBar" v-if="!checkLoginLoading()">
    <div class="container">
      <div class="row">
        <nav class="navbar navbar-expand-lg" data-mdb-right="true">
          <div class="container">
            <a class="navbar-brand" href="#">
              <img alt="" src="@/assets/Logo.svg" height="45" />
            </a>
            <button
              id="moblie_menu_btn"
              class="navbar-toggler"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#navbarNav"
              aria-controls="navbarNav"
              aria-expanded="false"
              aria-haspopup="true"
              aria-label="Toggle navigation"
              @click="refreshFunc($event)"
              v-if="getCookies()"
            >
              <span class="navbar-toggler-icon navbar-dark"></span>
            </button>
            <div class="collapse navbar-collapse" v-if="getCookies()">
              <ul class="navbar-nav">
                <li class="nav-item">
                  <router-link to="/" active-class="active">{{
                    $t("header.home")
                  }}</router-link>
                  <div class="selected"></div>
                </li>
                <li class="nav-item">
                  <router-link to="/Games1" active-class="active">{{
                    $t("header.featuredGames")
                  }}</router-link>
                  <div class="selected"></div>
                </li>
                <li class="nav-item">
                  <router-link to="/Games2" active-class="active">{{
                    $t("header.sexualGames")
                  }}</router-link>
                  <div class="selected"></div>
                </li>
                <li class="nav-item">
                  <router-link to="/Contact" active-class="active">{{
                    $t("header.contactUs")
                  }}</router-link>
                  <div class="selected"></div>
                </li>
                <li class="nav-item">
                  <router-link to="/News" active-class="active">{{
                    $t("header.newsList")
                  }}</router-link>
                  <div class="selected"></div>
                </li>
                <li class="nav-item">
                  <a href="https://news.murax2.com/" target="_blank">
                    {{ $t("header.adultInfo") }}
                  </a>
                  <div class="selected"></div>
                </li>
                <li class="nav-item btns_li" v-if="!login">
                  <button
                    type="button"
                    class="btn btn-pink"
                    @click="login_PC_func"
                  >
                    {{ $t("header.login") }}
                  </button>
                  <button
                    type="button"
                    class="btn btn-pink"
                    @click="signUpClick"
                  >
                    {{ $t("header.signup") }}
                  </button>
                </li>

                <li
                  class="nav-item btns_li login_btns"
                  v-if="login"
                  @click="refreshFunc($event)"
                >
                  <button
                    type="button"
                    class="btn btn-pink"
                    @click="storedPointClick"
                  >
                    {{ $t("header.storedPoint") }}
                  </button>
                  <div class="account_btn" data-bs-toggle="dropdown">
                    <img :src="this.user_information_obj().avatar_url" />
                    <i class="bi bi-caret-down-fill"></i>
                  </div>
                  <ul class="dropdown-menu dropdown-menu-end">
                    <li class="account">
                      {{
                        this.user_information_obj().nickname +
                        "(" +
                        this.user_information_obj().username +
                        ")"
                      }}
                      <i class="bi bi-caret-up-fill"></i>
                    </li>
                    <hr />
                    <li class="point" :key="this.refreshKey">
                      {{ this.user_information_obj().total_points + " P" }}
                      <i
                        @click="storedPointClick"
                        class="bi bi-plus-square-fill"
                      ></i>
                    </li>
                    <a
                      href="https://para-member-dev.para-daily.com/profile"
                      target="_blank"
                    >
                      <li>{{ $t("header.accountManagement") }}</li>
                    </a>
                    <router-link to="/MyGames" class="myGames">
                      <li>
                        {{ $t("header.myGames") }}
                      </li>
                    </router-link>
                    <hr />
                    <li @click="logoutPC">{{ $t("header.logout") }}</li>
                  </ul>
                </li>
              </ul>
            </div>
          </div>
        </nav>
      </div>
    </div>
  </header>
  <div class="collapse" id="navbarNav" v-if="getCookies()">
    <ul class="navbar-nav">
      <li class="nav-item">
        <router-link to="/" active-class="active" @click="clickMenu">{{
          $t("header.home")
        }}</router-link>
        <div class="selected"></div>
      </li>
      <li class="nav-item">
        <router-link to="/Games1" active-class="active" @click="clickMenu">{{
          $t("header.featuredGames")
        }}</router-link>
        <div class="selected"></div>
      </li>
      <li class="nav-item">
        <router-link to="/Games2" active-class="active" @click="clickMenu">{{
          $t("header.sexualGames")
        }}</router-link>
        <div class="selected"></div>
      </li>
      <li class="nav-item">
        <router-link to="/Contact" active-class="active" @click="clickMenu">{{
          $t("header.contactUs")
        }}</router-link>
        <div class="selected"></div>
      </li>
      <li class="nav-item">
        <router-link to="/News" active-class="active" @click="clickMenu">{{
          $t("header.newsList")
        }}</router-link>
        <div class="selected"></div>
      </li>
      <li class="nav-item">
        <!-- <router-link to="/AdultInfo" active-class="active" @click="clickMenu">{{
          $t("header.adultInfo")
        }}</router-link> -->
        <a href="https://news.murax2.com/" target="_blank" @click="clickMenu">
          {{ $t("header.adultInfo") }}
        </a>
        <div class="selected"></div>
      </li>
      <li class="nav-item btns_li" v-if="!login">
        <button type="button" class="btn btn-pink" @click="loginMoblie">
          {{ $t("header.login") }}
        </button>
        <button type="button" class="btn btn-pink" @click="signUpClick">
          {{ $t("header.signup") }}
        </button>
      </li>
      <div class="login_btns_moblie" v-if="login">
        <hr />
        <li class="point nav-item" :key="this.refreshKey">
          <span>
            {{ this.user_information_obj().total_points + " P" }}
          </span>
          <button type="button" class="btn btn-pink" @click="storedPointClick">
            {{ $t("header.storedPoint") }}
          </button>
        </li>
        <hr />
        <li
          class="account nav-item"
          data-bs-toggle="collapse"
          data-bs-target="#navbarNav_account_moblie"
        >
          {{
            this.user_information_obj().nickname +
            "(" +
            this.user_information_obj().username +
            ")"
          }}
          <i class="bi bi-caret-down-fill"></i>
        </li>
      </div>
    </ul>
    <ul id="navbarNav_account_moblie" class="collapse navbar-nav">
      <a href="https://para-member-dev.para-daily.com/profile" target="_blank">
        <li class="nav-item">{{ $t("header.accountManagement") }}</li>
      </a>
      <router-link to="/MyGames" class="myGames">
        <li class="nav-item" @click="clickMenu">
          {{ $t("header.myGames") }}
        </li>
      </router-link>
      <li class="nav-item" @click="logoutMoblie">{{ $t("header.logout") }}</li>
    </ul>
  </div>
</template>

<script>
import $ from "jquery";
import axios from "axios";
import { reactive } from "vue";
let THIS;
const tokenData = reactive({
  token_type: "",
  expires_in: "",
  access_token: "",
  refresh_token: "",
});
export default {
  inject: [
    "refresh_token_func",
    "setCookie_token_func",
    "login_PC_func",
    "user_information_obj",
  ],
  name: "HeaderBar",
  data() {
    return {
      login: false,
      refreshKey: 0,
    };
  },
  setup() {
    window.addEventListener("message", (e) => {
      if (
        e.origin !== window.location.origin ||
        !Object.keys(e.data).includes("access_token")
      ) {
        return;
      }
      const { token_type, expires_in, access_token, refresh_token } = e.data;
      tokenData.token_type = token_type;
      tokenData.expires_in = expires_in;
      tokenData.access_token = access_token;
      tokenData.refresh_token = refresh_token;
      THIS.setCookie_token_func(tokenData);
      THIS.getUserData();
      location.reload();
    });
  },
  mounted() {
    THIS = this;
    if (this.login == false) {
      // if (window.localStorage.getItem("MURA_access_token"))
      //   this.getUserData(window.localStorage.getItem("MURA_access_token"));
      if (this.$cookies.get("MURA_access_token"))
        this.getUserData(this.$cookies.get("MURA_access_token"));
      else if (this.$cookies.get("MURA_refresh_token")) {
        this.refresh_token_func(tokenData, this.getUserData);
      }
    }
  },
  methods: {
    getCookies() {
      return this.$cookies.get("cover18_check_auth");
    },
    checkLoginLoading() {
      return this.$router.currentRoute.value.path.toLowerCase() == "/login";
    },
    clickMenu() {
      $("#moblie_menu_btn").click();
    },
    loginMoblie() {
      this.login_PC_func();
      this.clickMenu();
    },
    logoutMoblie() {
      this.logoutPC();
      $(".login_btns_moblie").find(".account").click();
      this.clickMenu();
    },
    logoutPC() {
      const options = {
        method: "POST",
        headers: {
          Authorization: "Bearer " + this.$cookies.get("MURA_access_token"),
        },
        url: "https://para-member-dev.para-daily.com/api/logout",
      };
      axios
        .request(options)
        .then(function () {
          // console.log(res);
          THIS.$cookies.remove("MURA_access_token");
          THIS.$cookies.remove("MURA_refresh_token");
          if (THIS.$router.currentRoute.value.path == "/MyGames") {
            THIS.$router.push({ name: "Home" });
          } else {
            THIS.login = false;
            location.reload();
          }
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    getUserData(accessToken) {
      const options = {
        method: "GET",
        headers: {
          Authorization: "Bearer " + (accessToken || tokenData.access_token),
        },
        url: "https://para-member-dev.para-daily.com/api/game-site/user/get",
      };
      axios
        .request(options)
        .then(function (res) {
          console.log(res);
          let datas = res.data;
          datas.total_points =
            Number(datas.remaining_point.paid_points) +
            Number(datas.remaining_point.free_points);
          THIS.user_information_obj(datas);
          THIS.login = true;
        })
        .catch(function (err) {
          // THIS.refreshToken();
          console.log(err);
        });
    },
    storedPointClick: function () {
      window.open("https://para-member-dev.para-daily.com/purchase");
    },
    signUpClick: function () {
      window.open("https://para-member-dev.para-daily.com/register");
    },
    refreshFunc: function (event) {
      if (
        $(event.delegateTarget).hasClass("account_btn") ||
        $(event.delegateTarget).hasClass("navbar-toggler")
      ) {
        this.refreshKey++;
      }
    },
  },
};
</script>

<style scoped lang="scss">
.btn-pink {
  --bs-btn-color: #ff3885;
  --bs-btn-bg: #ffffff;
  --bs-btn-border-color: #ffffff;
  --bs-btn-hover-color: #ff3885;
  --bs-btn-hover-bg: #ffdae7;
  --bs-btn-hover-border-color: #ffdae7;
  --bs-btn-active-color: #ff3885;
  --bs-btn-active-bg: #ffdae7;
  --bs-btn-active-border-color: #dfdbdb;
  --bs-btn-active-shadow: inset 0 3px 5px #ffffff;
  --bs-btn-disabled-color: #ff3885;
  --bs-btn-disabled-bg: #ff5187;
  --bs-btn-disabled-border-color: #ff5187;
}
.headerBar {
  background-color: #ff3885;
  height: 100px;
  display: flex;
  align-items: center;
}
.navbar-toggler {
  margin-left: 0.5em;
  margin-right: 0.5em;
  border-color: #ffffff;
}

.nav-item {
  > a {
    font-weight: bold;
    font-size: 1.3rem;
    color: #ffffff;
    text-decoration: none;
    margin: 0 13px;
  }
  button {
    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
    font-weight: bold;
    font-size: 1.1rem;
    margin: 0 13px;
  }
  .selected {
    height: 5px;
    margin: 0 15px;
    background-color: #ffa0c2;
    opacity: 0;
    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
  }
  > .active ~ .selected {
    opacity: 0.9;
  }
  &:hover .selected {
    opacity: 0.9;
  }
}
.navbar-collapse {
  position: relative;
  .btns_li {
    position: absolute;
    right: 0;
  }
  .login_btns {
    display: flex;
    align-items: center;
    .account_btn {
      display: flex;
      align-items: center;
      &:hover img {
        opacity: 0.9;
      }
      &:hover .bi-caret-down-fill {
        color: #ffdae7;
      }
      img {
        margin-left: 2px;
        margin-right: 2px;
        cursor: pointer;
        height: 38px;
        border-radius: 50%;
        transition: all 0.3s;
      }
      .bi-caret-down-fill {
        color: #ffffff;
        cursor: pointer;
      }
    }
    .dropdown-menu {
      width: 95%;
      border-radius: 3px;
      padding: unset;
      color: #ff3885;
      font-weight: 900;
      .account,
      .point {
        display: flex;
        justify-content: space-between;
      }
      .point {
        color: #fa79fe;
        cursor: default;
        i {
          transition: all 0.2s;
          cursor: pointer;
          &:hover {
            color: #ff3885;
            transform: scale(1.1);
          }
        }
      }
      li {
        padding-left: 0.8em;
        padding-right: 0.8em;
        padding-top: 0.5em;
        padding-bottom: 0.5em;
        cursor: pointer;
        &:not(.point) {
          &:hover {
            background-color: #ffdae7;
          }
        }
      }
      a {
        color: #ff3885;
        text-decoration: none;
      }
      hr {
        margin: unset;
      }
    }
  }
}

#navbarNav {
  position: absolute;
  z-index: 999;
  width: 100%;
  background-color: #ff3885;
  .navbar-nav {
    opacity: 0;
  }
  .nav-item {
    margin-top: 5px;
    margin-bottom: 5px;
    .selected {
      margin: 0 auto;
      width: 4em;
    }
  }
  .btns_li {
    flex-direction: row !important;
    justify-content: center;
  }
  .login_btns_moblie {
    font-weight: bold;
    color: #ffffff;
    padding-top: 0.5em;
    hr {
      margin: unset;
      border-bottom: 1px solid #ffffff;
    }
    .point {
      cursor: default;
      padding-top: 1em;
      padding-bottom: 1em;
      button {
        margin-left: 1.5em;
      }
    }
    .account {
      padding-top: 0.5em;
      padding-bottom: 0.5em;
      cursor: pointer;
    }
  }
}
#navbarNav_account_moblie {
  background-color: #f7f6ef;
  font-weight: bold;
  li {
    padding-top: 0.1em;
    padding-bottom: 0.1em;
    cursor: pointer;
  }
  a {
    color: unset;
    text-decoration: none;
  }
}
.show .navbar-nav {
  opacity: 1 !important;
}

@media (max-width: 1400px) {
  .navbar-brand img {
    height: 40px;
  }
  .nav-item {
    > a {
      font-size: 1.2rem;
      margin: 0 10px;
    }
    button {
      font-size: 1rem;
      margin: 0 10px;
    }
    .selected {
      margin: 0 12px;
    }
  }
}
@media (max-width: 1200px) {
  .navbar-brand img {
    height: 35px;
  }
  .nav-item {
    > a {
      font-size: 1.1rem;
      margin: 0 5px;
    }
    button {
      font-size: 1rem;
      margin: 0 5px;
    }
    .selected {
      margin: 0 7px;
    }
  }
}
</style>
